import React, { Component } from "react";
import "./login.scss";
import { connect } from "react-redux";
import Router from "next/router";
class Login extends Component {
	constructor() {
		super();
		this.state = {
			username: "nishant@gmail.com",
			password: "12345"
		};
	}

	handleSubmitClick = () => {
		let json_str = getCookie("authorisedUser");
		let authorisedUser = JSON.parse(json_str);
		let validUser = authorisedUser.some(data => {
			return (
				data.username === this.state.username &&
				data.password === this.state.password
			);
		});
		this.setCookie("username", this.state.username, 1);
	};

	setCookie = (cname, cvalue, exdays) => {
		let d = new Date();
		d.setTime(d.getTime() + exdays * 60 * 1000);
		let expires = "expires=" + d.toUTCString();
		document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
		Router.replace("/");
	};

	handleUserName = e => {
		this.setState({
			username: e.target.value
		});
	};
	handlePassword = e => {
		this.setState({
			password: e.target.value
		});
	};

	render() {
		return (
			<div className="page-login">
				<div>
					<input
						type="email"
						value={this.state.username}
						onChange={e => this.handleUserName(e)}
						placeholder="Enter email"
						required
					/>
					<input
						type="password"
						value={this.state.password}
						onChange={e => this.handlePassword(e)}
						placeholder="Enter username"
						required
					/>
					<button
						className="btn btn-primary"
						onClick={() => this.handleSubmitClick()}
					>
						Submit
					</button>
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	const { imageList } = state;
	return { imageList };
}

export default connect(mapStateToProps)(Login);
